:root {
    --cor_01: #1a2730;
    --cor_02: #45586c;
    --cor_03: #424048;
    --cor_04: #b0cee2;
    --cor_05: #a63e1b;
    --cor_06: #e95d2c;
    --cor_07: #f8cc53;
    --cor_08: #e7ad0c;
    --cor_09: #fff;
    --cor_10: #000;
}

/* === BOTÕES === */
button {
    width: 22vw;
    height: 6vh;
    border-radius: 2vh;
    background-color: var(--cor_08);
    border: 1px solid var(--cor_08);
    font-size: 1.2em;
    color: #1a2730;
    cursor: pointer;
}
button:hover {
    background-color: var(--cor_06);
    border: 1px solid var(--cor_06);
    transition: 0.4s;
    transform: scale(1.05);
}

@media (max-width: 768px) {
    button {
        font-size: 0.8em;
    }

}

/* === MENU SUSPENSO === */
.menu {
    width: 60vw;
    display: flex;
    justify-content: end;
    align-items: center;
    position: relative;
    z-index: 10;
}
.menu-toggle {
    color: var(--cor_09);
    font-size: 2em;
    cursor: pointer;
    display: none;
}
.nav {
    width: 60vw;
    display: flex;
    justify-content: end;
    align-items: center;
}
.nav-list {
    list-style-type: none;
    width: 50vw;
    display: flex;
    justify-content: end;
}
.nav-item{
    height: auto;
    text-align: end;
    margin-right: 20px;
}
.nav-link {
    color: var(--cor_09);
    text-decoration: none;
    font-size: 1.2em;
}
.nav-link:hover {
    color: var(--cor_04);
}
  
@media (max-width: 768px) {
  .menu-toggle {
    display: block;
    position:absolute;
  }

  .nav {
    display: none;
    flex-direction: column;
    background-color: var(--cor_01);
    margin-top: 25vh;
  }

  .nav.open {
    display: flex;
  }

  .nav-list {
    flex-direction: column;
  }
}


/* ===> BOTÃO DE ROLAGEM <=== */
.content {
    text-align: center;
    padding: 20px;
}
.scrollToTopBtn {
    position: fixed;
    bottom: 20px;
    right: 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 50%;
    padding: 10px 15px;
    font-size: 24px;
    cursor: pointer;
    display: none; /* Inicialmente escondido */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    z-index: 100;
}
.scrollToTopBtn:hover {
    background-color: #0056b3;
}

/* ===> BOTÃO DE WHATSAPP <=== */
.whatsappBtn {
    position: fixed;
    bottom: 75px;
    right: 0px; /* Alinha à esquerda, pode mudar para right para alinhar à direita */
    background-color: #25D366; /* Cor oficial do WhatsApp */
    border-radius: 15px 0 0 15px;
    padding: 15px 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    z-index: 100;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: var(--cor_09);
}
.whatsappBtn img {
    width: 40px; /* Tamanho do ícone do WhatsApp */
    height: 40px;
}
/* Estilo para quando o botão de WhatsApp é clicado */
.whatsappBtn:hover {
    background-color: #128C7E; /* Cor mais escura do WhatsApp */
}